/* Contact.css */
.contact-section {
  padding: 60px 20px;
  background: #f8f9fa;
  text-align: center;
}

.contact-section h2 {
  font-size: 2.5rem;
  margin-bottom: 10px;
}

.contact-section p {
  font-size: 1.1rem;
  margin-bottom: 30px;
  color: #555;
}

.contact-form {
  max-width: 600px;
  margin: 0 auto 30px auto;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.contact-form input,
.contact-form textarea {
  padding: 12px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 8px;
  width: 100%;
}

.contact-form textarea {
  min-height: 120px;
  resize: vertical;
}

.contact-form button {
  padding: 12px;
  font-size: 1rem;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.3s;
}

.contact-form button:hover {
  background-color: #0056b3;
}

.contact-info {
  color: #333;
  font-size: 1rem;
}

/* ===== MAP SECTION ===== */
.map-section {
  padding: 80px 10%;
  background: #fff;
  text-align: center;
}

.map-section .section-title {
  font-size: 2rem;
  color: #333;
  margin-bottom: 1.5rem;
}

.map-container iframe {
  width: 100%;
  max-width: 100%;
  border: none;
  border-radius: 16px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
}
